import { Col, Row } from 'antd';
import React, { useEffect, useState } from 'react';
import { LeftCircleFilled } from '@ant-design/icons';
import AlumniCard from '../../UiComponents/Others/AlumniCard';
import Alumni2015 from '../../../Data/Alumni2015';
import Alumni2016 from '../../../Data/Alumni2016';
const AlumniDetails = (props) => {
  const [database, setdatabase] = useState([]);
  const batch = props.batch;
  useEffect(() => {
    if (batch === 2015) {
      setdatabase(Alumni2015);
    } else if (batch === 2016) {
      setdatabase(Alumni2016);
    }
  }, [props.batch]);
  console.log(database);
  return (
    <div className='alumni-details container'>
      <h4>
        <div className='back-btn' onClick={props.back}>
          <LeftCircleFilled className='icon' />
        </div>
        Alumni from the batch {props.batch} - {props.batch + 4}
      </h4>
      <div className='card-container '>
        <Row gutter={8}>
          {database.map((value, index) => {
            return (
              <Col md={8}>
                <AlumniCard
                  imgSrc={value.imgSrc}
                  name={value.name}
                  headline={value.headline}
                  branch={value.branch}
                  about={value.about}
                  liLink={value.liLink}
                  ghLink={value.ghLink}
                  igLink={value.igLink}
                  fbLink={value.fbLink}
                />
              </Col>
            );
          })}
          <Col md={8}></Col>
        </Row>
      </div>
    </div>
  );
};

export default AlumniDetails;
