import { Col, Row } from 'antd';
import React, { useState } from 'react';
//animation
import { motion } from 'framer-motion';
import InfoCard from '../UiComponents/Others/InfoCard';
import AlumniDetails from '../SectionComponents/Alumni/AlumniDetails';
import { Helmet } from 'react-helmet';

const AboutAlumni = () => {
  const [options, setoptions] = useState(true);
  const [batch2015, setbatch2015] = useState(false);
  const [batch2016, setbatch2016] = useState(false);
  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      className='about-common-page about-alumni'
    >
      <Helmet>
        <title>Alumni of Ginnovation Club</title>
        <meta
          name='description'
          content="Creating an engaged, supportive alumni network is crucial to an institution's sucess. Ginnovation is proud of it's alumni."
        />
      </Helmet>
      <div className='page-top alumni-top'>
        <div className='pattern'></div>
        <motion.header
          initial={{ y: -100, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ delay: 0.2, duration: 0.5 }}
        >
          <h2>About Our Alumni</h2>
          <p>Nobody is bothered about an institution more than its alumni</p>
        </motion.header>
      </div>
      {options && (
        <div className='container alumni'>
          <Row gutter={8}>
            <Col md={6}></Col>
            <Col md={6}>
              <motion.div
                initial={{ y: 100, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ duration: 0.5 }}
              >
                <div
                  onClick={() => {
                    setbatch2015(true);
                    setoptions(false);
                    setbatch2016(false);
                  }}
                >
                  <InfoCard
                    head='Batch'
                    title='2015-19'
                    description='Card description with lots of great facts and interesting details.'
                  />
                </div>
              </motion.div>
            </Col>
            <Col md={6}>
              <motion.div
                initial={{ y: 100, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ duration: 0.5, delay: 0.2 }}
              >
                <div
                  onClick={() => {
                    setbatch2016(true);
                    setoptions(false);
                    setbatch2015(false);
                  }}
                >
                  <InfoCard
                    head='Batch'
                    title='2016-20'
                    description='Card description with lots of great facts and interesting details.'
                  />
                </div>
              </motion.div>
            </Col>
            <Col md={6}></Col>
          </Row>
        </div>
      )}
      {batch2015 && (
        <AlumniDetails
          batch={2015}
          back={() => {
            setbatch2015(false);
            setoptions(true);
          }}
        />
      )}
      {batch2016 && (
        <AlumniDetails
          batch={2016}
          back={() => {
            setbatch2016(false);
            setoptions(true);
          }}
        />
      )}
    </motion.div>
  );
};

export default AboutAlumni;
